<template>
  <div>
    <h1>404 Not Found</h1>
    <p>{{ randomMessage }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      randomMessage: "",
    };
  },
  mounted() {
    this.generateRandomMessage();
  },
  methods: {
    generateRandomMessage() {
      const messages = [
        "Oops! Looks like you're lost. Congratulations!",
        "This page does not exist. Are you sure you're in the right place? Obviously not!",
        "404 Error: Page not found. But don't worry, we'll find it together! Just kidding, you're on your own!",
        "You've stumbled upon a secret page that doesn't exist. Shh, don't tell anyone! Oh wait, there's no one to tell!",
        "Looks like you've taken a wrong turn. Let's get you back on track! Or not, who needs directions anyway?",
        "Page not found. Maybe it's on vacation? Probably sipping margaritas on a beach somewhere!",
        "Uh-oh! The page you're looking for seems to have vanished into thin air. Poof!",
        "404 Error: Page not found. Time to go on a treasure hunt! Good luck finding that non-existent treasure!",
      ];

      this.randomMessage =
        messages[Math.floor(Math.random() * messages.length)];
    },
  },
};
</script>
